import React, { Component } from 'react'
import DataRow from './DataRow';

export default class DataTable extends Component {
    constructor(props){
        super();
    }

    render() {
        return (
            <div className="flex-col-container">
                <h3 style={{borderBottom: "2px solid var(--brandingColor2)"}}>{this.props.name}</h3>
                {
                    Object.keys(this.props.data).map(tag => {
                        return <DataRow name={tag} value={this.props.data[tag]} key={tag} />
                    })
                }
            </div>
        )
    }
}
