import React, { Component } from 'react';

export default class Heading extends Component {
    render() {
        return (
            <div className="centered-container message-box">
                <h1 className="textshadow">Keep your images private!</h1>
                <p className="textshadow">Most digital cameras embed metadata within images, such as camera model, date and time, etc. Images taken with cellphones often include GPS information as well. Many users would consider this data to be a privacy concern. With this tool you can view and remove EXIF data, and feel secure sharing your images without the risk of being tracked. <strong>None of the images you upload to this site will be saved, nor will their associated EXIF data.</strong></p>
            </div>
        )
    }
}
