import React, { Component } from 'react'

import DataTable from "./DataTable";
import ImageThumbnailBox from './ImageThumbnailBox';

export default class ResultsBox extends Component {
    constructor(props) {
        super();

        this.ref = React.createRef();
    }

    scrollToMe() {
        window.scrollTo(0, this.ref.current.offsetTop)
    }

    componentDidUpdate() {
        this.scrollToMe();
    }

    googleMapQ(){
        console.log(this.props.gpsRaw);
        let latR = this.props.gpsRaw["1"] === "N" ? "%2B" : "-";
        let lat0 = this.props.gpsRaw["2"][0][0] / this.props.gpsRaw["2"][0][1];
        let lat1 = this.props.gpsRaw["2"][1][0] / this.props.gpsRaw["2"][1][1];
        let lat2 = this.props.gpsRaw["2"][2][0] / this.props.gpsRaw["2"][2][1];

        let lonR = this.props.gpsRaw["3"] === "E" ? "%2B" : "-";
        let lon0 = this.props.gpsRaw["4"][0][0] / this.props.gpsRaw["4"][0][1];
        let lon1 = this.props.gpsRaw["4"][1][0] / this.props.gpsRaw["4"][1][1];
        let lon2 = this.props.gpsRaw["4"][2][0] / this.props.gpsRaw["4"][2][1];

        return `${latR}${lat0}%C2%B0+${lat1}'+${lat2}%22,+${lonR}${lon0}%C2%B0+${lon1}'+${lon2}`;
    }

    render() {
        return (
            <section ref={this.ref}>
                <div className="results-border" height="10px"></div>
                {this.props.exif ? <div className="centered-container padded-container" style={{ minHeight: "100vh" }}>
                    <h1>Results</h1>
                    <div className="flex-row-container">
                        <div className="flex-col-container" style={{ minWidth: "50%" }}>
                            {Object.keys(this.props.exif["0th"]).length > 0 ? <DataTable name="0th" data={this.props.exif["0th"]} /> : null}
                            {Object.keys(this.props.exif["Exif"]).length > 0 ? <DataTable name="Exif" data={this.props.exif["Exif"]} /> : null}
                            {Object.keys(this.props.exif["GPS"]).length > 0 ? <DataTable name="GPS" data={this.props.exif["GPS"]} /> : null}
                        </div>
                        <div className="flex-col-container" style={{maxWidth: "50%", paddingLeft: "15px"}}>
                            {this.props.image ? <ImageThumbnailBox imgsrc={this.props.image} mode={this.props.mode} /> : null}
                            {this.props.gpsRaw && ["1","2","3","4"].every(e => Object.keys(this.props.gpsRaw).indexOf(e) > -1) ? <div>
                                <h3>GPS Map</h3>
                                <iframe title="GPSmap"
                                    width="100%"
                                    height="450px"
                                    frameborder="1" style={{border:0}}
                                    src={`https://www.google.com/maps/embed/v1/place?key=[Your Google Maps Api Key Here]
        &q=${this.googleMapQ()}`}></iframe>
                            </div> : null}
                        </div>
                    </div>
                </div> : null}
            </section>
        )
    }
}
