import React, { Component } from 'react'

export default class DataRow extends Component {
    constructor(props){
        super();
    }
    render() {
        return (
            <div className="flex-row-container" style={{ marginTop: "5px", borderBottom: "1px solid #ccc" }}>
                <label style={{ width: "50%", fontSize: "12px" }}><b>{this.props.name}:</b></label>
                <label style={{ fontSize: "12px", width: "50%", wordWrap: "break-word" }}>{this.props.value}</label>
            </div>
        )
    }
}
