import React, { Component } from 'react'

import ResultsBox from "./ResultsBox";
import UploadBox from "./UploadBox";
import Branding from "./Branding";
import Heading from "./Heading";

export default class App extends Component {
    constructor(props){
        super();

        this.state = {
            exif: undefined,
            image: undefined,
            gpsRaw: undefined,
            resMode: undefined
        };
    }

    handleChange(data){
        this.setState({ exif: data.exif, image: data.jpeg, gpsRaw: data.gpsRaw, resMode:data.mode });
    }

    render() {
        return (
            <div>
                <section class="site-masthead boxshadow-inset" style={{ backgroundColor: "var(--brandingColor1)" }}>
                    <Branding />
                    <Heading />
                    <UploadBox onChange={this.handleChange.bind(this)} />
                </section>
                <ResultsBox exif={this.state.exif} gpsRaw={this.state.gpsRaw} image={this.state.image} mode={this.state.resMode} />
            </div>
        )
    }
}

