import React, { Component } from 'react';

export default class UploadBox extends Component {
    constructor(props) {
        super();

        this.state = {
            mode: 0,
            fileIcon: "fas fa-hdd",
            fileSelected: false,
            error: undefined
        };

        this.modeNameMap = { 0: "View", 1: "Scrub All", 2: "Scrub GPS Only" };
        this.modeIconMap = { 0: "fas fa-search", 1: "fas fa-soap", 2: "fas fa-map-marked-alt" }
    }

    handleExif(exifRecvd) {
        this.props.onChange(exifRecvd);
    }

    handleSubmit(e) {
        e.preventDefault();
        if(!this.state.fileSelected) return;
        
        let path = this.state.mode > 0 ? `scrub${this.state.mode === 1 ? "" : "/gps"}` : "view";
        console.log(path);

        fetch(`/upload/${path}`,
            {
                method: "POST",
                body: new FormData(e.target)
            }
        )
            .then((response) => {
                return response.json();
            })
            .then((data) => {
                if (!data.error) this.handleExif(data);
                else{
                    this.setState({error: data.error});
                }
            })
            .catch(error => console.log(error));
    }

    handleFileChange(e) {

        if (e.target.files[0] === undefined) {
            this.setState({ error: undefined, mode: 0, fileIcon: "fas fa-hdd", fileSelected: false });
        } else {
            this.setState({ error: undefined, fileIcon: "fas fa-check-square", fileSelected: true });
        }
    }

    handleModeClick() {
        if(!this.state.fileSelected) return;
        let newMode = this.state.mode + 1;
        this.setState({ mode: newMode % Object.keys(this.modeNameMap).length });
    }

    render() {
        return (
            <div>
                <form className="padded-container-v15" onSubmit={(e) => this.handleSubmit(e)}>
                    <div className="flex-row-container centered-container">
                        <input type="file" id="file" name="imgupload" accept=".jpg, .jpeg" onChange={(e) => this.handleFileChange(e)} />
                        <label className={`clickable explain-card${this.state.fileSelected ? " file-selected" : " no-file-selected"}`} for="file">
                            <i className={`${this.state.fileIcon} textshadow ${this.state.fileSelected ? "" : "bounce"}`} style={{ "--duration": "1.5s" }}></i>
                            <div>
                                <h1 className="textshadow">{this.state.fileSelected ? "Change image" : "Select image"}</h1>
                                <p className="textshadow">Maximum file size: 2MB</p>
                            </div>
                        </label>
                        <div className={`explain-card${this.state.fileSelected ? " widget-enabled clickable" : " widget-disabled"}`} onClick={() => this.handleModeClick()}>
                            <i className={`${this.state.fileSelected ? `${this.modeIconMap[this.state.mode]} bounce` : "fas fa-cogs"} textshadow`}
                                style={{ "--duration": "1.2s" }}></i>
                            <div style={{ visibility: `${this.state.fileSelected ? "visible" : "hidden"}` }}>
                                <h1 className="textshadow">Select mode</h1>
                                <p className="textshadow">Mode: <span style={{ color: "var(--brandingColor2)" }}>{this.modeNameMap[this.state.mode]}</span></p>
                            </div>
                        </div>
                        <button className={`explain-card${this.state.fileSelected ? " widget-enabled clickable" : " widget-disabled"}`}
                            type="submit">
                            <i className={`fas fa-cloud-upload-alt textshadow ${this.state.fileSelected ? "bounce" : ""}`} style={{ "--duration": "1.5s" }}></i>
                            <div style={{ visibility: `${this.state.fileSelected ? "visible" : "hidden"}` }}>
                                <h1 className="textshadow">Upload</h1>
                                <p className="textshadow">Your results will appear below</p>
                            </div>
                        </button>
                    </div>
                        {this.state.mode === 1 ? <div className="centered-container" style={{textAlign: "center"}}>
                            <label for="orientation" style={{color: "white"}}><input type="checkbox" id="orientation" name="orientation" value="preserveOrientation" />Preserve orientation</label>
                    </div> : null}

                    {this.state.error && <div className="centered-container" style={{textAlign: "center", color: "var(--brandingColor2)"}}>
                        <b>Error:</b> {this.state.error.message}
                    </div>}
                </form>
            </div>
        )
    }
}
