import React, { Component } from 'react'

export default class ImageThumbnailBox extends Component {
    constructor(props){
        super();
    }

    render() {
        return (
            <div className="flex-col-container" style={{padding: "5px"}}>
                <div className="flex-col-container" style={{float: "right"}}>
                    <h3>Image</h3>
                    <img src={this.props.imgsrc} alt="" style={{width: "35%"}} />
                    {this.props.mode === "scrub" && <a href={this.props.imgsrc} download="download.jpg">Download image <i className="fas fa-download"></i></a>}
                </div>
            </div>
        )
    }
}
